<template>
  <div class="icons-list">
    <a-tooltip placement="top" v-if="visible_edit" @click="v_instance.onEditing(object)" title="Modificar Elemento">
      <a-icon type="edit" style="padding-right: 5px" theme="twoTone"/>
    </a-tooltip>
    <a-tooltip placement="top" v-if="visible_view" title="Mostrar Elemento">
      <a-icon type="eye" style="padding-right: 5px" theme="twoTone" twoToneColor="green"/>
    </a-tooltip>
    <a-tooltip placement="top" v-if="visible_delete" title="Borrar Elemento">
      <a-icon type="delete" style="padding-right: 5px" @click="showDeleteConfirm" theme="twoTone"
              twoToneColor="red"/>
    </a-tooltip>
  </div>
</template>
<script>
  import * as utils from '../../../../entities/utils/utils';
  import * as model_factory from "../../../../entities/models";
  import { eventBus  } from "../../../../main";


  export default {
    name: "action_buttons",
    props: {
      object: Object,
      v_instance: Object,
      class_name: {
        type: String,
        default: "BaseModel"
      },
      visible_edit: {
        type: Boolean,
        default: true
      },
      visible_delete: {
        type: Boolean,
        default: true
      },
      visible_view: {
        type: Boolean,
        default: true
      },
    },
    data() {
      return {
        model:{}
      }
    },
    methods: {
      showDeleteConfirm() {
        console.log("entro a la funcion eliminar");
        var idEntidad = 0;

        if(this.class_name == "Proceso"){
            idEntidad = this.model.id_entidad;
        }else if (this.class_name == "Reporte"){
            idEntidad = this.model.proceso.id_entidad;
        }

        if(eventBus.user.id_entidad == idEntidad || eventBus.esGestor || eventBus.esAdmin){
            console.log("entro al id que comprueba si es gestora, etc..");
            let _this = this;
            this.$confirm({
                title: 'Desea eliminar este elemento?',
                icon: "delete",
                // icon:()=>{return ( <a-icon type="delete" style="color:red"/> )},
                okText: 'Yes',
                okType: 'danger',
                class: 'delete_confirm',
                cancelText: 'No',
                async onOk() {
                  console.log("entro a la condicion de OK");
                    try {
                      console.log("entro al try de la condicion de OK");
                      console.log("El modelo es:"+ _this.model);
                      console.log("El nombre de modelo es:"+ _this.model.class_name);
                        const response = await _this.model.delete();
                        console.log("entro al comando : const response = await _this.model.delete()");
                        utils.process_response(response, 'delete')
                        _this.v_instance.load_data();
                        console.log("termino en la funcion eliminar");
                    } catch (error) {
                        utils.process_error(error)
                    }
                },
                onCancel() {

                },
            });
        }else{
            utils.openNotificationWithIcon(
                        "error",
                        "Eliminar elemento.",
                        "Usted no puede eliminar este elemento."
                    );
        }
      },
    },
    mounted() {
      this.model = model_factory.instance(this.class_name)
      this.model.set_attributes(this.object)
    }
  }
</script>

<style scoped>
  /*.delete_confirm*/

</style>




